import { Group, PointLight, SpotLight, AmbientLight, HemisphereLight } from 'three';

class BasicLights extends Group {
    constructor(...args) {
        // Invoke parent Group() constructor with our args
        super(...args);

        const dir = new SpotLight(0xffffff, 10, 8, 0.8, 1, 1);
        const ambi = new AmbientLight(0x000000, 1.32);
        const hemi = new HemisphereLight(0xff0000, 0xff0000, 1.5);
        const point = new PointLight(0xffffff, 10, 1);
        point.position.set(0, 0, 0);

        dir.position.set(5, 5, 5);
        dir.target.position.set(0, 0, 0);

        this.add(point);

        this.add(ambi);
        this.add(hemi);
        this.add(dir);
    }
}

export default BasicLights;