/**
 * app.js
 *
 * This is the first file loaded. It sets up the Renderer,
 * Scene and Camera. It also starts the render loop and
 * handles window resizes.
 *
 */
import { WebGLRenderer, PerspectiveCamera, Vector3 } from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { SeedScene } from 'scenes';
import { Shape } from 'objects';
import HANJI from './hanji0.jpeg';
// import HANJI from './hanji2.png';
import { GodRaysEffect, EffectComposer, SMAAEffect, EffectPass, RenderPass } from "postprocessing";
import * as THREE from 'three';

// Initialize core ThreeJS components

// fov : Number, aspect : Number, near : Number, far : Numbe
const camera = new PerspectiveCamera();
const renderer = new WebGLRenderer({
    powerPreference: "high-performance",
    antialias: false,
    // stencil: false,
    // depth: false
});

// Set up renderer, canvas, and minor CSS adjustments
const canvas = renderer.domElement;
canvas.style.display = 'block'; // Removes padding below canvas
document.body.style.margin = 0; // Removes margin around page
document.body.style.overflow = 'hidden'; // Fix scrolling
document.body.appendChild(canvas);

renderer.setPixelRatio(window.devicePixelRatio);
renderer.setSize(canvas.width, canvas.height);
// renderer.toneMapping = THREE.ACESFilmicToneMapping;
// renderer.toneMappingExposure = 1;
const scene = new SeedScene();

const texture = new THREE.TextureLoader().load(HANJI);
texture.wrapS = THREE.RepeatWrapping;
texture.wrapT = THREE.RepeatWrapping;
texture.repeat.set(1, 1);

const geometry0 = new THREE.SphereGeometry(1);
const material0 = new THREE.MeshBasicMaterial({
    color: 0xffffff,
    // map: texture,
    transparent: true,
    opacity: 0.4,
});
const sphere = new THREE.Mesh(geometry0, material0);
sphere.position.set(0, 0, 0);
scene.add(sphere);

const length = 2.8;
// const color = 0xff0000;

const color = 0xffccaa;
const geometry = new THREE.BoxGeometry(length, length, length);
const material = new THREE.MeshBasicMaterial({
    color: 0xffffff,
    map: texture,
    transparent: true,
    opacity: 0.5,

});
const cube = new THREE.Mesh(geometry, material);
cube.position.set(0, 0, 0);
scene.add(cube);

let length1 = 2.9;
const geometry1 = new THREE.BoxGeometry(length1, length1, length1);
const material1 = new THREE.MeshBasicMaterial({
    color: color,
    map: texture,
    transparent: true,
    opacity: 0.7
});
const cube1 = new THREE.Mesh(geometry1, material1);
cube1.position.set(0, 0, 0);
scene.add(cube1);

// const shape = new Shape(this, 10);
// shape.draw_cube(0x000000, 0, 0, 0, 1.5);
// scene.add(shape);

// let circleGeo = new THREE.CircleGeometry(5, 5);
// let circleMat = new THREE.MeshBasicMaterial({ color: 0xffccaa });
// let circle = new THREE.Mesh(circleGeo, circleMat);
// circle.position.set(-5, -5, -5)
// scene.add(circle);
const godraysEffect = new GodRaysEffect(camera, cube1, {
    exposure: 0.3,
    resolutionScale: 0.5, //The resolution scale.
    density: 0.8, //The density of the light rays.
    decay: 0.97, // //An illumination decay factor.
    weight: 0.09, // //A light ray weight factor.
    samples: 50 //The number of samples per pixel.
})

// let areaImage = new Image();
// areaImage.src = SMAAEffect.areaImageDataURL;


const composer = new EffectComposer(renderer);
composer.setSize(canvas.width, canvas.height);
const effectPass = new EffectPass(camera, godraysEffect);
effectPass.renderToScreen = true;
const renderPass = new RenderPass(scene, camera)
composer.addPass(renderPass);
composer.addPass(effectPass);



// Set up camera
const dist = 15;
// camera.position.set(dist, dist, dist);
camera.position.set(dist, 7.5, dist);
camera.lookAt(new Vector3(0, 0, 0));


// Set up controls
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;
controls.enablePan = false;
// controls.minDistance = 4;
// controls.maxDistance = 16;
controls.update();

// Render loop
const onAnimationFrameHandler = (timeStamp) => {
    controls.update();
    renderer.render(scene, camera);
    scene.update && scene.update(timeStamp);
    window.requestAnimationFrame(onAnimationFrameHandler);
    composer.render(0.1);
};
window.requestAnimationFrame(onAnimationFrameHandler);


// Resize Handler
const windowResizeHandler = () => {
    const { innerHeight, innerWidth } = window;
    renderer.setSize(innerWidth, innerHeight);
    camera.aspect = innerWidth / innerHeight;
    camera.updateProjectionMatrix();
};
windowResizeHandler();
window.addEventListener('resize', windowResizeHandler, false);